* {
  margin: 0;
  padding: 0;
}

header {
  background-color:#262626;
  box-shadow: 0px 3px 10px #464646;
}

@keyframes go-back {
  0% {
    transform: translateX(20px);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes go-back-cards {
  0% {
    transform: translateX(600px);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes tremer {
  0% {margin-left: 0;}
  25% {margin-left: 7px;}
  50% {margin-left: 0;}
  75% {margin-left: -7px;}
  100% {margin-left: 0;}
}

.navbar {
  display: flex;
  justify-content: center;
  padding: 0.5rem 6rem;
  text-align:center;
  align-items: center;
}

.logo {
  display: flex;
  align-items: center;
}

.logo h2 {
  color: #D8F20F;
  text-transform: uppercase;
  font-weight: 400;
  font-style: italic;
  letter-spacing: 1px;
  text-align: center;
  font-family: 'Merriweather Sans', sans-serif;
}

.nav-list {
  display: flex;
  align-items: center;
  text-decoration: none;
  font-family: 'Merriweather Sans', sans-serif;
  font-weight: 300;

}

.nav-list ul {
  display: flex;
  justify-content: center;
  list-style: none;
}

.nav-item{
  margin: 0 15px;
  color: white;
  transition: 0.3s;
}

.nav-item:hover {
  color: #D8F20F;
  cursor: pointer;
}

.flexbox-container {
  background-color: #F2E313;
  color: #262626;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  font-family: 'Merriweather Sans', sans-serif;
}

.flex-item {
  width: 33%;
  margin: 20px;
  border-right: 2px solid white;
  animation: go-back 3s;
}

.item-1 {
  min-height: 100px;
  border: black;
  animation: go-back 3s;
}

.item-1 img {
  border-radius: 56%;
  width: 300px;
}

.item-2 {
  min-width: 360px;
  min-height: 100px;
  margin-top: 10px;
  text-align: center;
  justify-content: center;
  font-weight: 400;
  
}

.item-2 h2{
  border-bottom: 1px solid white;
  color: white;
}

.item-2 p {
  margin-top: 10px;
}

.item-3 h2{
  border-bottom: 1px solid white;
  color: white;
  text-align: center;
}
.item-3 {
  margin-top: 10px;
  width: 100px;
  text-align: center;
  justify-content: center;
  font-style: italic;
}

.item-3 ul {
  margin-top: 10px;
  list-style: none;
}

.flexbox-container-2 {
  background-color: #262626;
  color: #262626;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  font-family: 'Merriweather Sans', sans-serif;
  background-attachment: fixed;
}



.flexbox-container-2 h2{
  color: white;
  min-width: 100%;
  margin-bottom: 10px;
}

div.cards {
  display: flex;
  padding: 32px;
  padding-top: 5px;
  animation: go-back-cards 1.5s;
}

div.cards section.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: white;
  padding: 32px 16px;
  border-radius: 8px;
  max-height: 256px;
  margin-left: 20px;
}

div.cards section.card:first-child{
  margin-left: 0;
}

div.cards section.card .icon {
  width: 64px;
  height: 64px;
}

div.cards section.card img{
  width: 100%;
}



div.cards section.card button {
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  border-radius: 32px;
  border: 0;
  cursor: pointer;
  font-size: 80%;
  font-family: 'Merriweather Sans', sans-serif;
  font-size: 700;
  font-weight: bold;
  background-color: #D8F20F;
  color: #262626;
  transition: 0.3s;
}

div.cards section.card button:hover{
  background-color: #262626;
  color: #D8F20F;
}

div.cards section.card button:disabled {
  background-color: #262626;
  text-decoration: line-through;
  color: white;
  cursor: default;
}

div.cards section.card p{
  color: #262626;
  margin: 16px 0;
}

div.cards section.card img {
  animation: go-back 3s;
}

.title-2 {
  padding: 10px;
  color: white;
  background-color: #262626;
  text-align: center;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 600px){
  div.cards{
    flex-direction: column;
  }

  div.cards section.card{
    margin-left: 0;
    margin-bottom: 25px;
    width: 240px;
    transition: 0.3s;
    animation: go-back 2s;
  }

  div.cards section.card:last-child{
    margin-bottom: 0;
  }
}

.flexbox-container-3 {
  background-color: white;
  color: #262626;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  font-family: 'Merriweather Sans', sans-serif;
  background-attachment: fixed;
}

.flexbox-container-3 h2{
  width: 100%;
  text-align: center;
  margin-bottom: 10px;
  align-items: center;
}

.form-contato{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-contato input{
  font-family: 'Merriweather Sans', sans-serif;
  width: 350px;
  height: 36px;
  margin-bottom: 10px;
  border-radius: 8px;
  padding: 10px;
  border: 0px solid #262626;
  background-color: rgb(240, 240, 240);
}

.form-contato input:focus{
  outline: none;
  
}

.form-contato .btn-enviar {
  border-radius: 8px;
  border: none;
  background-color: #D8F20F;
  width: 100px;
  height: 50px;
  font-family: 'Merriweather Sans', sans-serif;
  font-style: italic;
  font-weight: 700;
}

footer {
  background-color: #262626;
  height: 36px;
  color: white;
  font-family: 'Merriweather Sans', sans-serif;
  font-style: italic;
  font-weight: 700;
  text-align: center;
  padding: 20px;
}